import Vue from 'vue'
import VueResource from 'vue-resource'

Vue.use(VueResource)
Vue.http.options.root = '/api'
Vue.http.options.emulateJSON = false

export default {
  showProblem (offset, limit) {
    return ajax('issue/show/' + offset + '/' + limit, 'get')
  },
  getLinkage (id = 0) {
    return ajax('wx_user/linkage/' + id, 'get')
  },
  addIssue (title, description, userId = null) {
    return ajax('issue/store', 'post', {
      body: {
        title,
        description,
        wx_user_id: userId
      }
    })
  },
  // 保存用户信息
  saveUser (name, phone, email, province, city, area, course, oldSchool) {
    return ajax('wx_user/store', 'post', {
      options: {
        name,
        phone,
        email,
        province,
        city,
        area,
        course,
        old_school: oldSchool
      }
    })
  }
}

/**
 ajax 请求
 @param url
 @param type get|post|put|jsonp ....
 @param options options = {
                      body: request body
                      options: ..,
                      succCallBack: Function
                      errCallBack: Function
                    }
 @return Promise
*/

function ajax (url, type, options) {
  return new Promise(function (resolve, reject) {
    options = options || {}
    if (options.body === undefined) {
      options.body = options.options
      options.options = undefined
    }
    Vue.http[type](url, options.body, options.options).then(res => {
      // 请求成功
      resolve(res)
      if (options.succCallBack !== undefined) {
        options.succCallBack(res)
      }
    }, res => {
      // 请求失败
      reject(res)
      if (options.errCallBack !== undefined) {
        options.errCallBack(res)
      }
    })
  })
}



