<template>
  <div>
    <THeader title="提问" bgColor="#B06E6E"></THeader>
    <div class="weui-cells body">
      <div class="weui-cell">
          <div class="weui-cell__bd">
              <input v-model="title" class="weui-input" type="text" placeholder="请输入标题">
          </div>
      </div>
      <div class="weui-cell">
            <div class="weui-cell__bd">
                <textarea v-model="content" class="weui-textarea" placeholder="请输入问题详情" rows="5"></textarea>
            </div>
        </div>
    </div>
    <div class="tip" v-if="userId == undefined">当前是匿名模式 填写个人信息可以让老师更好的帮助你哦<a href="#/perfectinfo">去填写个人信息</a></div>
    <div class="btn"><a href="javascript:;" class="weui-btn weui-btn_primary" @click="submit">发布</a></div>
    <TFooter></TFooter>
  </div>
</template>

<script>
  import THeader from '../components/THeader'
  import TFooter from '../components/TFooter'
  import api from '../api'
  export default{
    components: {
      THeader, TFooter
    },
    data () {
      return {
        userId: undefined,
        title: '',
        content: ''
      }
    },
    methods: {
      submit () {
        api.addIssue(this.title, this.content, this.userId).then(res => {
          this.$message('老师已经收到了你的问题，请等候回复')
          this.$router.push({name: 'home'})
        }, res => {
          this.$message.error(res.data.message)
        })
      }
    },
    mounted () {
      this.userId = window.localStorage.zszx_userId
    }
  }
</script>

<style scoped>
.body{
  margin-top: 5px!important;
}
.btn{
  padding: 10px 20px;
}
.tip{
  color: #666;
  font-size: 12px;
  padding: 10px 15px;
}
.tip a{
  margin-left: 5px;
}
</style>


