<template>
  <transition name="el-message-fade">
    <div class="el-message" v-show="visible" @mouseenter="clearTimer" @mouseleave="startTimer">
      <img class="el-message__icon" :src="typeImg" alt="">
      <div class="el-message__group">
        <p>{{ message }}</p>
        <div v-if="showClose" class="el-message__closeBtn el-icon-close" @click="close"></div>
      </div>
    </div>
  </transition>
</template>

<script type="text/babel">
  export default {
    data () {
      return {
        visible: false,
        message: '',
        duration: 3000,
        type: 'info',
        onClose: null,
        showClose: false,
        closed: false,
        timer: null
      }
    },

    computed: {
      typeImg () {
        return require(`../assets/${this.type}.svg`)
      }
    },

    watch: {
      closed (newVal) {
        if (newVal) {
          this.visible = false
          this.$el.addEventListener('transitionend', this.destroyElement)
        }
      }
    },

    methods: {
      destroyElement () {
        this.$el.removeEventListener('transitionend', this.destroyElement)
        this.$destroy(true)
        this.$el.parentNode.removeChild(this.$el)
      },

      close () {
        this.closed = true
        if (typeof this.onClose === 'function') {
          this.onClose(this)
        }
      },

      clearTimer () {
        clearTimeout(this.timer)
      },

      startTimer () {
        if (this.duration > 0) {
          this.timer = setTimeout(() => {
            if (!this.closed) {
              this.close()
            }
          }, this.duration)
        }
      }
    },

    mounted () {
      this.startTimer()
    }
  }
</script>

<style scoped>
.el-message {
  box-shadow: 0 2px 4px rgba(0,0,0,.12),0 0 6px rgba(0,0,0,.04);
  min-width: 300px;
  padding: 10px 12px;
  box-sizing: border-box;
  border-radius: 2px;
  position: fixed;
  left: 50%;
  top: 20px;
  transform: translateX(-50%);
  -webkit-transition: opacity .3s,-webkit-transform .4s;
  transition: opacity .3s,-webkit-transform .4s;
  transition: opacity .3s,transform .4s;
  transition: opacity .3s,transform .4s,-webkit-transform .4s;
  background-color: #fff;
  overflow: hidden;
}
.el-message__icon {
    width: 40px;
    height: 40px;
    position: absolute;
    left: 0;
    top: 0;
}
.el-message__group {
    margin-left: 38px;
    position: relative;
}
.el-message__group > p{
  font-size: 14px;
  line-height: 20px;
  margin: 0 34px 0 0;
  white-space: nowrap;
  color: #8492a6;
  text-align: justify;
}
.el-message__closeBtn{
  top: 3px;
  right: 0;
  position: absolute;
  cursor: pointer;
  color: #C0CCDA;
  font-size: 14px
}
.el-message__closeBtn:hover{
  color: #99A9BF
}
.el-message-fade-enter,.el-message-fade-leave-active{
  opacity:0;
  -webkit-transform:translate(-50%,-100%);
  transform:translate(-50%,-100%)
}
</style>



