<template>
  <header class="header" :style="{backgroundColor: bgColor}">
    {{Title}}
  </header>
</template>

<script>
  export default{
    props: {
      Title: String,
      bgColor: String
    }
  }
</script>

<style scoped>
.header {
  height: 48px;
  background-color: #BC2F2E;
  line-height: 48px;
  color: #fff;
  padding-left: 20px;
  border-bottom: 1px solid #e1e1e1;
}
</style>


