<template>
  <div>
    <THeader title="淮南师范学院 招生咨询"></THeader>
    <ul class="list">
      <li class="item" v-for="item in list">
        <header>
          <!-- <div class="face">
            <img src="https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=870609829,3308433796&fm=116&gp=0.jpg">
          </div> -->
          <div class="info">
            <strong class="name">{{item.wx_users != null ? item.wx_users.name : '匿名'}}</strong>
            <div class="time">{{item.created_at}}</div>
          </div>
        </header>
        <h3 class="title">{{item.title}}</h3>
        <section class="content">
          {{item.description}}
        </section>
        <section class="reply">
          管理员回复：{{item.comment.answer}}
        </section>
        <footer>
          <!-- <span class="views"><i class="iconfont icon-iconfontordinaryliulan"></i>浏览：100</span> -->
        </footer>
      </li>
      <div class="weui-loadmore" v-if="loading">
        <i class="weui-loading"></i>
        <span class="weui-loadmore__tips">正在加载</span>
      </div>
      <div class="weui-loadmore weui-loadmore_line" v-if="isEnd">
        <span class="weui-loadmore__tips">已到底部了</span>
      </div>
      <TFooter></TFooter>
    </ul>
    <div class="add" @click="$router.push({name: 'create'})">
      <div class="input">我要咨询</div>
      <div class="btn">发布</div>
    </div>
  </div>
</template>

<script>
  import THeader from '../components/THeader'
  import TFooter from '../components/TFooter'
  import api from '../api'
  export default{
    components: {
      THeader, TFooter
    },
    data () {
      return {
        loading: false,
        list: [],
        offset: 0,
        limit: 5,
        isEnd: false,
        flag: true
      }
    },
    methods: {
      loadMore () {
        if (this.flag) {
          this.flag = false
          api.showProblem(this.offset, this.limit).then(res => {
            if (res.data.length === 0) {
              this.isEnd = true
            } else {
              this.list = this.list.concat(res.data)
              this.offset += this.limit
            }
            this.flag = true
            this.loading = false
          })
        }
      }
    },
    mounted () {
      window.onscroll = (e) => {
        let scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop
        if (document.documentElement.scrollHeight === document.documentElement.clientHeight + scrollTop) {
          if (!this.isEnd) {
            this.loading = true
            this.loadMore()
          }
        }
      }
      api.showProblem(0, this.limit).then(res => {
        this.list = res.data
        this.offset += this.limit
      })
    }
  }
</script>

<style>
  .list{
    margin-bottom: 80px;
  }
  .item {
    list-style: none;
    background-color: #fff;
    margin: 15px 0;
    padding: 10px 16px;
    border-bottom: 1px solid #e1e1e1;
  }

  .item .face {
    float: left;
  }
  .item .title{
    text-align: center;
    font-size: 14px;
    margin: 15px 0;
  }
  .item .face img {
    width: 40px;
    height: 40px;
    background-color: #e3e1de;
    vertical-align: middle;
    border-radius: 40px;
  }
  .item .name {
    font-size: 14px;
  }

  .item .time {
    color: #aaa;
    line-height: 1.2;
    font-size: 12px;
  }

  .item .content {
    margin: 10px 0;
    font-size: 16px;
    color: #464646;
    line-height: 150%;
  }

  .item .reply {
    position: relative;
    margin-top: 5px;
    background-color: #f0f0f0;
    padding: 8px;
    font-size: 13px;
    border-radius: 5px;
  }

  .item .reply:before {
    content: "";
    width: 0;
    height: 0;
    position: absolute;
    top: -10px;
    left: 8%;
    border: 5px solid #f0f0f0;
    border-color: transparent transparent #f0f0f0;
  }

  .item footer {
    margin-top: 10px;
    color: #157c8c;
  }

  .item .views {
    display: flex;
    align-items: center;
    line-height: 15px;
    font-size: 12px;
  }

  .item .views i {
    margin-right: 5px;
  }

  .add {
    position: fixed;
    bottom: 0;
    left: 0;
    background-color: #f3f3f3;
    border-top: 1px solid #e1e1e1;
    z-index: 10;
    width: 100%;
    display: flex;
    padding: 10px;
    box-sizing: border-box;
  }

  .add .input {
    display: flex;
    flex: 1;
    height: 35px;
    border: #f1f1f1 1px solid;
    background-color: #fff;
    align-items: center;
    padding-left: 10px;
    color: #d1d1d1;
    font-size: 14px;
  }

  .add .btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 35px;
    background-color: #38f;
    color: #fff;
    font-size: 16px;
    border-top: 1px solid #38f;
    border-bottom: 1px solid #38f;
  }
</style>



