<template>
  <div>
    <div class="tip">你好，信息填写的越完整老师才知道怎么帮助你。</div>
    <div class="base_info weui-cells weui-cells_form">
      <div class="weui-cell">
        <div class="weui-cell__hd"><label class="weui-label">姓名<i class="require">*</i></label></div>
        <div class="weui-cell__bd">
            <input class="weui-input" type="text" placeholder="请输入你的姓名" v-model="name">
        </div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__hd"><label class="weui-label">手机<i class="require">*</i></label></div>
        <div class="weui-cell__bd">
            <input class="weui-input" type="tel" placeholder="请输入你的手机号" v-model="tel">
        </div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__hd"><label class="weui-label">邮箱</label></div>
        <div class="weui-cell__bd">
            <input class="weui-input" type="email" placeholder="请输入你的邮箱" v-model="email">
        </div>
      </div>
    </div>
    <div class="weui-cells page__category-content">
        <div class="city_box">
          <a class="weui-cell weui-cell_access js_item" @click="currCityMode = 'province',showCity = !showCity, changeCityMode()" href="javascript:;">
            <div class="weui-cell__bd">
                <p>所在地区</p>
            </div>
            <div class="weui-cell__ft">{{provinceName + ' ' + cityName + ' ' + areaName}}</div>
          </a>
          <div v-if="showCity" class="city">
            <div class="weui-navbar">
                <div @click="currCityMode = 'province'" class="weui-navbar__item" :class="{'weui-bar__item_on': currCityMode == 'province'}">
                    省份
                </div>
                <div @click="currCityMode = 'city'" class="weui-navbar__item" :class="{'weui-bar__item_on': currCityMode == 'city'}">
                    城市
                </div>
                <div @click="currCityMode = 'area'" class="weui-navbar__item" :class="{'weui-bar__item_on': currCityMode == 'area'}">
                    地区
                </div>
            </div>
            <div class="body">
              <span v-if="currCityMode == 'province'" @click="changeCityMode(item.name, item.zone_id, item.level)" v-for="item in provinceInfo">{{item.name}}</span>
              <span v-if="currCityMode == 'city'" @click="changeCityMode(item.name, item.zone_id, item.level)" v-for="item in cityInfo">{{item.name}}</span>
              <span v-if="currCityMode == 'area'" @click="changeCityMode(item.name, item.zone_id, item.level)" v-for="item in areaInfo">{{item.name}}</span>
            </div>
          </div>
        </div>
        <div class="weui-cell">
          <div class="weui-cell__hd"><label class="weui-label">考试成绩</label></div>
          <div class="weui-cell__bd">
              <input class="weui-input" type="number" placeholder="考试成绩" v-model="score">
          </div>
        </div>
        <div class="weui-cell">
          <div class="weui-cell__hd"><label class="weui-label">毕业学校</label></div>
          <div class="weui-cell__bd">
              <input class="weui-input" type="text" placeholder="毕业学校" v-model="school">
          </div>
        </div>
    </div>
    <div class="submit_btn">
      <a href="javascript:;" class="weui-btn weui-btn_mini weui-btn_primary submit" @click="submit">提交</a><a href="#" class="skip">跳过</a>
    </div>
    <TFooter></TFooter>
  </div>
</template>

<script>
  import TFooter from '../components/TFooter'
  import api from '../api'
  export default{
    components: {
      TFooter
    },
    data () {
      return {
        showCity: false,
        provinceInfo: [],
        cityInfo: [],
        areaInfo: [],
        currCityMode: '',
        provinceName: '',
        cityName: '',
        areaName: '',
        name: '',
        tel: '',
        email: '',
        score: '',
        school: ''
      }
    },
    methods: {
      submit () {
        if (this.name === '' || this.tel === '') {
          this.$message({
            message: '姓名和手机号是必填项哦',
            type: 'warning'
          })
        } else {
          api.saveUser(this.name, this.tel, this.email, this.provinceName, this.cityName, this.areaName, this.score, this.school).then(res => {
            // 请求成功
            this.$message('保存成功')
            window.localStorage.zszx_userId = res.data.wx_user.id
            this.$router.push({name: 'home'})
          }, res => {
            console.log(res)
            this.$message.error(res.data.message)
          })
        }
      },
      changeCityMode (name, id = 0, level = 0) {
        api.getLinkage(id).then(res => {
          if (level === 0) {
            this.currCityMode = 'province'
            this.provinceName = ''
            this.provinceName = ''
            this.areaName = ''
            this.provinceInfo = res.data.regions
          } else if (level === 1) {
            this.provinceName = name
            this.cityName = ''
            this.areaName = ''
            this.currCityMode = 'city'
            this.cityInfo = res.data.regions
          } else if (level === 2) {
            this.cityName = name
            this.areaName = ''
            this.currCityMode = 'area'
            this.areaInfo = res.data.regions
          } else {
            this.areaName = name
            this.showCity = false
          }
        })
      }
    }
  }
</script>

<style scoped>
.require{
  color: #f30;
  font-style: normal;
  padding-left: 5px;
}
.tip{
  text-align: center;
  font-size: 12px;
  color: #666;
  padding: 10px 0 0 10px;
}
.base_info{
  margin-top: 10px;
}
.submit_btn{
  margin-top: 20px;
  text-align: center;
  position: relative;
}
.submit_btn a{
  display: inline-block;
}
.submit_btn .skip{
  color: #666;
  font-size: 12px;
  margin-left: 10px;
  position: absolute;
  top: 7px;

}
.submit_btn .submit{
  width: 120px;
}
.city_box{
  position: relative;
}
.city{
  z-index: 999;
  position: absolute;
  background-color: #fff;
  width: 100%;
  top: 46px;
  left: 0;
  border-top: solid 3px #3879D9;
}
.city .weui-navbar__item{
  height: 41px;
  line-height: 41px;
  padding: 0;
}
.city .body{
  padding: 51px 10px 10px 10px;
  background-color: #EAEAEA;
}
.city .body span{
  color: #333;
  padding: 0 10px;
  display: inline-block;
}
.weui-cells{
  overflow: inherit;
}
</style>


